.main {
    color: $grey-50;
    min-height: 538px;
    padding: 175px 100px 100px 0px;
    padding-left: 95px;
}

.main__h1 {
    font-size: 54px;
    line-height: 66px;
    font-weight: 700;
    padding-bottom: 30px;

    @media (max-width: $desktop-width) {
        font-size: 50px;
        line-height: 61px;
        padding-bottom: 29px;
    }
}

.main__text {
    font-size: 20px;
    line-height: 25px;
    font-weight: 400;

    @media (max-width: $desktop-width) {
        font-size: 18px;
        line-height: 22px;
    }
}
