.contacts-map {
    position: relative;
    flex-shrink: 0;
    width: 560px;
    height: 306px;
    overflow: hidden;
    cursor: pointer;

    @media (max-width: $desktop_width) {
        width: 324px;
        height: 335px;
    }

    @media (max-width: $tablet_width) {
        width: 290px;
        height: 316px;
    }
}

.contacts-map__iframe {
    width: 560px;
    height: 306px;

    @media (max-width: $desktop_width) {
        width: 324px;
        height: 335px;
    }

    @media (max-width: $tablet_width) {
        width: 290px;
        height: 316px;
    }
}

.contacts-map__img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
}

.contacts-map__pin {
    position: absolute;
    top: 70.7%;
    left: 44.6%;
    z-index: -1;
    width: 18px;
    height: 22px;
}
