*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

a {
  text-decoration: none;
  color: #011c40;
  box-sizing: border-box;
  cursor: pointer;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
p,
li {
  margin: 0;
  padding: 0;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat-Regular"), local("Montserrat Regular"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat-Medium"), local("Montserrat Medium"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat-SemiBold"), local("Montserrat SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: local("Montserrat-Bold"), local("Montserrat Bold"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
.visually-hidden {
  display: none;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 320px;
  margin: 0;
  padding: 0;
  overflow: auto;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  min-height: 100%;
  color: #011c40;
  background-color: #f9fbfd;
}

.page-header {
  position: relative;
  padding: 35px 0px 35px 0px;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 20px 0;
  }
}
@media (max-width: 767px) {
  .page-header {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
  }
}

@media (max-width: 1023px) {
  .page-header__logo-img {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .page-header__logo-img {
    width: 42px;
    height: 22px;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 33px 0;
}
@media (max-width: 1023px) {
  .page-header__wrapper {
    padding: 20px 0;
  }
}
@media (max-width: 767px) {
  .page-header__wrapper {
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
  }
}

.page-header__back-img img {
  position: absolute;
  top: 0;
  left: 0;
  height: 650px;
  object-fit: cover;
  object-position: center center;
  z-index: -1;
  background-size: auto auto;
  background-position: 50% 50%;
  background-color: #011c40;
  min-width: 100%;
}
@media (max-width: 1023px) {
  .page-header__back-img img {
    height: 638px;
    object-position: center top;
  }
}
@media (max-width: 767px) {
  .page-header__back-img img {
    height: 500px;
    object-position: center 75%;
  }
}

.page-header__burger {
  display: none;
  position: absolute;
  top: 25px;
  right: 15px;
  justify-self: center;
}
@media (max-width: 767px) {
  .page-header__burger {
    display: block;
  }
}

.page-header-menu {
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .page-header-menu {
    display: none;
  }
}

.page-header-menu__list {
  display: flex;
}

.page-header-menu__link {
  margin-left: 33px;
  color: #f9fbfd;
  min-width: 100%;
}
@media (max-width: 767px) {
  .page-header-menu__link {
    margin-left: 23px;
  }
}

.wrapper {
  padding: 0 103px;
  margin: 0 auto;
  width: 100%;
}
@media (max-width: 1023px) {
  .wrapper {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .wrapper {
    padding: 0 15px;
  }
}

.main {
  color: #f9fbfd;
  min-height: 538px;
  padding: 175px 100px 100px 0px;
  padding-left: 95px;
}

.main__h1 {
  font-size: 54px;
  line-height: 66px;
  font-weight: 700;
  padding-bottom: 30px;
}
@media (max-width: 1023px) {
  .main__h1 {
    font-size: 50px;
    line-height: 61px;
    padding-bottom: 29px;
  }
}

.main__text {
  font-size: 20px;
  line-height: 25px;
  font-weight: 400;
}
@media (max-width: 1023px) {
  .main__text {
    font-size: 18px;
    line-height: 22px;
  }
}

.travel {
  display: flex;
  align-items: center;
  padding-top: 120px;
  padding-bottom: 120px;
}

.travel__description {
  padding: 66px 90px 60px 0px;
}

.travel__h2 {
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  padding-bottom: 40px;
}

.travel__text {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

.travel__img img {
  min-width: 560px;
  height: 301px;
  background-color: #e3ecf6;
}
@media (max-width: 1023px) {
  .travel__img img {
    min-width: 324px;
    height: 264px;
  }
}
@media (max-width: 767px) {
  .travel__img img {
    min-width: 290px;
    height: 186px;
  }
}

.page-main {
  flex-grow: 1;
}

.page-footer {
  background-color: #011c40;
  padding: 50px 0px;
}

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-footer__nav-container {
  display: flex;
  align-items: center;
}

.page-footer__logo-img {
  margin-right: 7px;
}
@media (max-width: 1023px) {
  .page-footer__logo-img {
    margin-right: 2px;
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .page-footer__logo-img {
    width: 42px;
    height: 22px;
  }
}

.page-footer-menu__list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 5px;
}

.page-footer-menu__link {
  margin-left: 33px;
  color: #f9fbfd;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .page-footer-menu__link {
    margin-left: 23px;
    font-size: 12px;
    line-height: 15px;
  }
}

.social__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.social__img {
  margin-left: 33px;
}
@media (max-width: 1023px) {
  .social__img {
    margin-left: 23px;
  }
}
.social__img--fb {
  width: 11px;
  height: 22px;
}
@media (max-width: 1023px) {
  .social__img--fb {
    width: 9px;
    height: 16px;
  }
}
.social__img--inst {
  width: 20px;
  height: 20px;
}
@media (max-width: 1023px) {
  .social__img--inst {
    width: 16px;
    height: 16px;
  }
}
.social__img--twitter {
  width: 20px;
  height: 16px;
}
@media (max-width: 1023px) {
  .social__img--twitter {
    width: 16px;
    height: 13px;
  }
}

.benefits {
  padding: 120px 0px 120px 0px;
  background-color: #e3ecf6;
  background-image: url("../img/desktop/benefits_desk.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .benefits {
    background-image: url("../img/desktop/benefits_desk@2x.jpg");
  }
}
@media (max-width: 767px) {
  .benefits {
    padding: 89px 0 70px 0;
    background-image: url("../img/tablet/benefits_tab.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .benefits {
    background-image: url("../img/tablet/benefits_tab@2x.jpg");
  }
}
@media (max-width: 767px) {
  .benefits {
    padding: 58px 0 34px 0;
    background-image: url("../img/mobile/benefits_mob.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .benefits {
    background-image: url("../img/mobile/benefits_mob@2x.jpg");
  }
}

.benefits__h2 {
  text-align: center;
  margin-bottom: 60px;
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
}

.benefits-list {
  display: flex;
  justify-content: space-between;
}

.benefits-list__item {
  box-sizing: content-box;
  width: 360px;
  background-color: #f9fbfd;
  padding: 30px;
  display: grid;
  grid-template-rows: 32px auto auto;
  align-items: center;
  grid-row-gap: 20px;
}
.benefits-list__item:not(:first-child) {
  margin-left: 40px;
}
@media (max-width: 1023px) {
  .benefits-list__item {
    padding: 25px 20px;
  }
}

.benefits-list__h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  padding: 0;
}

.benefits-list__p {
  font-size: 14px;
  line-height: 20px;
}

.cruises {
  padding: 120px 0px;
}

.cruises__h2 {
  text-align: center;
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  margin-bottom: 60px;
}

.cruises-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cruises-list__h3 {
  position: absolute;
  bottom: 30px;
  left: 30px;
  color: #f9fbfd;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  z-index: 2;
}

.cruises-list__dl {
  display: none;
  color: #f9fbfd;
}

.cruises-list__dt {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

.cruises-list__dd {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 10px;
}

.cruises-list__wrapper {
  margin: 0 25px;
}

.cruises-list__button {
  display: none;
  width: 210px;
  height: 40px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  position: absolute;
  bottom: 35px;
  margin-top: 10px;
}

.cruises-list__item {
  width: 260px;
  min-height: 386px;
  margin: 15px;
  background-color: #011c40;
  position: relative;
}
@media (max-width: 1023px) {
  .cruises-list__item {
    width: 309px;
  }
}
@media (max-width: 767px) {
  .cruises-list__item {
    width: 270px;
    margin: 0 10px 30px 10px;
  }
}
.cruises-list__item:hover img {
  display: none;
}
.cruises-list__item:hover h3 {
  position: static;
  margin: 35px 25px 15px 25px;
}
.cruises-list__item:hover dl {
  display: block;
}
.cruises-list__item:hover a {
  display: block;
}

.cruises-list__item img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.button {
  padding: 11px 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  background-color: #f9fbfd;
  border: none;
  border-radius: 0;
  cursor: pointer;
}
.button:hover {
  outline: 4px solid #2770b4;
}
.button:focus {
  outline: 4px solid #2770b4;
}
.button:active {
  opacity: 0.7;
}

.booking {
  background-color: #e3ecf6;
  background-image: url("../img/desktop/booking-back_desk.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
@media (min-resolution: 1.5dppx) {
  .booking {
    background-image: url("../img/desktop/booking-back_desk@2x.jpg");
  }
}

.booking__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 60px 50px 60px;
}
@media (max-width: 1023px) {
  .booking__wrapper {
    flex-direction: column;
  }
}

.booking__h2 {
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .booking__h2 {
    font-size: 34px;
    line-height: 51px;
    margin-bottom: 20px;
  }
}

.booking__appeal {
  margin-right: 90px;
}
@media (max-width: 1023px) {
  .booking__appeal {
    margin-right: 0px;
    margin-bottom: 55px;
  }
}

.booking__appeal-text {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .booking__appeal-text {
    text-align: center;
  }
}

.booking__card-text {
  margin-bottom: 27px;
  color: #f9fbfd;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.booking__card {
  flex-shrink: 0;
  align-items: center;
  width: 403px;
  padding: 44px 50px 50px 50px;
  background-color: #011c40;
}

.booking-form {
  display: flex;
  flex-direction: column;
}

.booking-form input {
  margin: 0 0 20px 0;
  padding: 11px 16px 11px 16px;
  border: none;
  border-radius: 0;
}

.booking-form input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
}

.booking-form label[for=useragreement] {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 28px;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
}

.booking-form__checkbox {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  margin-right: 3px;
  background-color: #f9fbfd;
  border: 1px solid #011c40;
}
.booking-form__checkbox svg {
  display: none;
}

input[type=checkbox] {
  display: none;
}
input[type=checkbox]:checked ~ .booking-form__checkbox svg {
  display: block;
}

input[id=useragreement]:hover ~ .booking-form__checkbox {
  outline: 4px solid #2770b4;
}
input[id=useragreement]:focus ~ .booking-form__checkbox {
  outline: 4px solid #2770b4;
}
input[id=useragreement]:active ~ .booking-form__checkbox {
  opacity: 0.7;
}

.contacts__wrapper {
  display: flex;
  align-items: center;
  column-gap: 100px;
  margin: 120px 0;
}
@media (max-width: 1023px) {
  .contacts__wrapper {
    column-gap: 30px;
    min-width: 324px;
  }
}

.contacts-map {
  position: relative;
  flex-shrink: 0;
  width: 560px;
  height: 306px;
  overflow: hidden;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .contacts-map {
    width: 324px;
    height: 335px;
  }
}
@media (max-width: 767px) {
  .contacts-map {
    width: 290px;
    height: 316px;
  }
}

.contacts-map__iframe {
  width: 560px;
  height: 306px;
}
@media (max-width: 1023px) {
  .contacts-map__iframe {
    width: 324px;
    height: 335px;
  }
}
@media (max-width: 767px) {
  .contacts-map__iframe {
    width: 290px;
    height: 316px;
  }
}

.contacts-map__img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.contacts-map__pin {
  position: absolute;
  top: 70.7%;
  left: 44.6%;
  z-index: -1;
  width: 18px;
  height: 22px;
}

.contacts-text__h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 45px;
}

.contacts-text__p br {
  display: none;
}
@media (max-width: 1023px) {
  .contacts-text__p br {
    display: block;
  }
}
.contacts-text__p::before {
  content: url("../img/sprite.svg#pin");
}

.contacts-text__tel::before {
  content: url("../img/sprite.svg#tel");
}

.contacts-text__p,
.contacts-text__tel {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  padding-left: 28px;
  margin-bottom: 33px;
}
.contacts-text__p::before,
.contacts-text__tel::before {
  position: absolute;
  width: 18px;
  height: 22px;
  display: block;
  background-size: 18px 22px;
