.page-footer-menu__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 5px;
}

.page-footer-menu__link {
    margin-left: 33px;
    color: $grey-50;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    @media (max-width: $desktop-width) {
        margin-left: 23px;
        font-size: 12px;
        line-height: 15px;
    }
}
